.contact {
  padding: 6rem 0;
  background: linear-gradient(135deg, #000 0%, #0a0a0a 50%, #000 100%);
  position: relative;
  overflow: hidden;
}

.contact::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 20% 80%, rgba(0, 212, 255, 0.05) 0%, transparent 50%),
              radial-gradient(circle at 80% 20%, rgba(0, 212, 255, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

.contact-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  z-index: 1;
}

.contact-header {
  text-align: center;
  margin-bottom: 4rem;
}

.contact-title {
  font-family: 'Orbitron', monospace;
  font-size: 3rem;
  font-weight: 900;
  margin-bottom: 1rem;
  color: #00d4ff;
  text-shadow: 0 0 20px rgba(0, 212, 255, 0.3);
}

.contact-subtitle {
  font-size: 1.2rem;
  color: #ccc;
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
}

.contact-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
}

.contact-info {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.contact-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1.5rem;
  background: rgba(0, 212, 255, 0.05);
  border: 1px solid rgba(0, 212, 255, 0.2);
  border-radius: 15px;
  transition: all 0.3s ease;
  will-change: transform, box-shadow;
}

.contact-item:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 30px rgba(0, 212, 255, 0.15);
  border-color: rgba(0, 212, 255, 0.4);
}

.contact-icon {
  width: 50px;
  height: 50px;
  background: linear-gradient(45deg, #00d4ff, #0099cc);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  color: #000;
  flex-shrink: 0;
}

.contact-details h3 {
  font-family: 'Rajdhani', sans-serif;
  font-weight: 600;
  font-size: 1.1rem;
  color: #fff;
  margin-bottom: 0.3rem;
}

.contact-details p {
  font-size: 0.9rem;
  color: #ccc;
  margin: 0;
}

.social-links-section {
  margin-top: 2rem;
}

.social-title {
  font-family: 'Rajdhani', sans-serif;
  font-weight: 600;
  font-size: 1.2rem;
  color: #fff;
  margin-bottom: 1rem;
  text-align: center;
}

.social-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
}

.social-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1.5rem;
  background: rgba(0, 212, 255, 0.05);
  border: 1px solid rgba(0, 212, 255, 0.2);
  border-radius: 15px;
  text-decoration: none;
  color: #fff;
  transition: all 0.3s ease;
  will-change: transform, box-shadow;
}

.social-link:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(0, 212, 255, 0.2);
  border-color: #00d4ff;
  color: #00d4ff;
}

.social-icon {
  font-size: 2rem;
  color: #00d4ff;
  margin-bottom: 0.5rem;
  transition: all 0.3s ease;
  will-change: transform;
}

.social-link:hover .social-icon {
  transform: scale(1.1);
}

.social-name {
  font-family: 'Rajdhani', sans-serif;
  font-weight: 500;
  font-size: 0.9rem;
  text-align: center;
}

.contact-visual {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.contact-image {
  position: relative;
  width: 400px;
  height: 400px;
  border-radius: 20px;
  background: linear-gradient(45deg, #00d4ff, #0099cc);
  padding: 10px;
  animation: float 6s ease-in-out infinite;
  will-change: transform;
}

.contact-image::before {
  content: '';
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  background: linear-gradient(45deg, #00d4ff, #0099cc, #0066ff);
  border-radius: 25px;
  z-index: -1;
  animation: glow 3s ease-in-out infinite alternate;
  will-change: box-shadow;
}

.contact-photo {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  object-fit: cover;
  border: 3px solid #000;
}

.contact-cta {
  text-align: center;
  margin-top: 3rem;
  padding: 2rem;
  background: rgba(0, 212, 255, 0.05);
  border: 1px solid rgba(0, 212, 255, 0.2);
  border-radius: 20px;
}

.cta-title {
  font-family: 'Orbitron', monospace;
  font-size: 1.5rem;
  font-weight: 700;
  color: #00d4ff;
  margin-bottom: 1rem;
  text-shadow: 0 0 10px rgba(0, 212, 255, 0.3);
}

.cta-description {
  font-size: 1rem;
  color: #ccc;
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.cta-button {
  background: linear-gradient(45deg, #00d4ff, #0099cc);
  border: none;
  color: #000;
  font-family: 'Rajdhani', sans-serif;
  font-weight: 600;
  font-size: 1.1rem;
  padding: 1rem 2rem;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-decoration: none;
  display: inline-block;
  will-change: transform, box-shadow;
}

.cta-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 30px rgba(0, 212, 255, 0.3);
}

/* Otimizações para mobile */
@media (max-width: 768px) {
  .contact {
    padding: 4rem 0;
  }

  .contact-container {
    padding: 0 1rem;
  }

  .contact-title {
    font-size: 2.5rem;
  }

  .contact-subtitle {
    font-size: 1.1rem;
  }

  .contact-content {
    grid-template-columns: 1fr;
    gap: 3rem;
  }

  .contact-info {
    gap: 1.5rem;
  }

  .contact-item {
    padding: 1.2rem;
  }

  .contact-icon {
    width: 45px;
    height: 45px;
    font-size: 1.3rem;
  }

  .contact-details h3 {
    font-size: 1rem;
  }

  .contact-details p {
    font-size: 0.85rem;
  }

  .social-grid {
    grid-template-columns: repeat(3, 1fr);
    gap: 0.8rem;
  }

  .social-link {
    padding: 1.2rem;
  }

  .social-icon {
    font-size: 1.8rem;
  }

  .social-name {
    font-size: 0.8rem;
  }

  .contact-image {
    width: 280px;
    height: 280px;
  }

  .contact-cta {
    padding: 1.5rem;
  }

  .cta-title {
    font-size: 1.3rem;
  }

  .cta-description {
    font-size: 0.9rem;
  }

  .cta-button {
    font-size: 1rem;
    padding: 0.8rem 1.5rem;
  }

  /* Otimizar animações em mobile */
  .contact-image {
    animation: float 8s ease-in-out infinite;
  }

  .social-link:hover .social-icon {
    transform: scale(1.05);
  }
}

@media (max-width: 480px) {
  .contact {
    padding: 3rem 0;
  }

  .contact-title {
    font-size: 2rem;
  }

  .contact-subtitle {
    font-size: 1rem;
  }

  .contact-item {
    padding: 1rem;
  }

  .contact-icon {
    width: 40px;
    height: 40px;
    font-size: 1.2rem;
  }

  .contact-details h3 {
    font-size: 0.9rem;
  }

  .contact-details p {
    font-size: 0.8rem;
  }

  .social-grid {
    grid-template-columns: repeat(3, 1fr);
    gap: 0.6rem;
  }

  .social-link {
    padding: 1rem;
  }

  .social-icon {
    font-size: 1.5rem;
  }

  .social-name {
    font-size: 0.7rem;
  }

  .contact-image {
    width: 220px;
    height: 220px;
  }

  .contact-cta {
    padding: 1.2rem;
  }

  .cta-title {
    font-size: 1.2rem;
  }

  .cta-description {
    font-size: 0.85rem;
  }

  .cta-button {
    font-size: 0.9rem;
    padding: 0.7rem 1.2rem;
  }

  /* Desabilitar animações complexas em dispositivos muito pequenos */
  .contact-image {
    animation: float 10s ease-in-out infinite;
  }

  .social-link:hover .social-icon {
    transform: none;
  }
}

/* Otimizações de performance */
@media (prefers-reduced-motion: reduce) {
  .contact-item,
  .social-link,
  .contact-image,
  .contact-image::before,
  .cta-button,
  .social-icon {
    animation: none;
    transition: none;
  }
} 