.about {
  padding: 6rem 0;
  background: linear-gradient(135deg, #000 0%, #0a0a0a 50%, #000 100%);
  position: relative;
  overflow: hidden;
}

.about::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 20% 50%, rgba(0, 212, 255, 0.05) 0%, transparent 50%),
              radial-gradient(circle at 80% 20%, rgba(0, 212, 255, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

.about-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
}

.about-content {
  position: relative;
  z-index: 2;
}

.about-title {
  font-family: 'Orbitron', monospace;
  font-size: 3rem;
  font-weight: 900;
  margin-bottom: 2rem;
  color: #00d4ff;
  text-shadow: 0 0 20px rgba(0, 212, 255, 0.3);
}

.about-description {
  font-size: 1.2rem;
  line-height: 1.8;
  margin-bottom: 2rem;
  color: #ccc;
}

.about-highlights {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.highlight-item {
  background: rgba(0, 212, 255, 0.1);
  border: 1px solid rgba(0, 212, 255, 0.3);
  border-radius: 10px;
  padding: 1.5rem;
  text-align: center;
  transition: all 0.3s ease;
  will-change: transform, box-shadow;
}

.highlight-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 212, 255, 0.2);
  border-color: #00d4ff;
}

.highlight-icon {
  font-size: 2.5rem;
  color: #00d4ff;
  margin-bottom: 1rem;
}

.highlight-title {
  font-family: 'Rajdhani', sans-serif;
  font-weight: 600;
  font-size: 1.1rem;
  margin-bottom: 0.5rem;
  color: #fff;
}

.highlight-description {
  font-size: 0.9rem;
  color: #aaa;
  line-height: 1.5;
}

.about-image {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.image-frame {
  position: relative;
  width: 400px;
  height: 400px;
  border-radius: 20px;
  background: linear-gradient(45deg, #00d4ff, #0099cc);
  padding: 10px;
  animation: float 6s ease-in-out infinite;
  will-change: transform;
}

.image-frame::before {
  content: '';
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  background: linear-gradient(45deg, #00d4ff, #0099cc, #0066ff);
  border-radius: 25px;
  z-index: -1;
  animation: glow 3s ease-in-out infinite alternate;
  will-change: box-shadow;
}

.about-photo {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  object-fit: cover;
  border: 3px solid #000;
}

.about-stats {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  margin-top: 3rem;
}

.stat-item {
  text-align: center;
  padding: 1.5rem;
  background: rgba(0, 212, 255, 0.05);
  border: 1px solid rgba(0, 212, 255, 0.2);
  border-radius: 15px;
  transition: all 0.3s ease;
  will-change: transform, box-shadow;
}

.stat-item:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(0, 212, 255, 0.15);
}

.stat-number {
  font-family: 'Orbitron', monospace;
  font-size: 2.5rem;
  font-weight: 900;
  color: #00d4ff;
  margin-bottom: 0.5rem;
  text-shadow: 0 0 10px rgba(0, 212, 255, 0.3);
}

.stat-label {
  font-family: 'Rajdhani', sans-serif;
  font-weight: 500;
  color: #ccc;
  font-size: 1rem;
}

.skills-overview {
  margin-top: 4rem;
}

.skills-title {
  font-family: 'Orbitron', monospace;
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 3rem;
  color: #00d4ff;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.skill-card {
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(0, 212, 255, 0.2);
  border-radius: 15px;
  padding: 2rem;
  text-align: center;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.skill-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(0, 212, 255, 0.1), transparent);
  transition: left 0.5s;
}

.skill-card:hover::before {
  left: 100%;
}

.skill-card:hover {
  border-color: rgba(0, 212, 255, 0.5);
  box-shadow: 0 10px 30px rgba(0, 212, 255, 0.2);
}

.skill-icon {
  font-size: 3rem;
  color: #00d4ff;
  margin-bottom: 1rem;
  text-shadow: 0 0 10px rgba(0, 212, 255, 0.5);
}

.skill-title {
  font-family: 'Rajdhani', sans-serif;
  font-size: 1.3rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: #fff;
}

.skill-description {
  font-size: 1rem;
  color: #ccc;
  line-height: 1.6;
}

.about-cta {
  text-align: center;
  margin-top: 4rem;
  padding: 3rem;
  background: rgba(0, 212, 255, 0.05);
  border: 1px solid rgba(0, 212, 255, 0.2);
  border-radius: 20px;
  backdrop-filter: blur(10px);
}

.cta-text {
  font-size: 1.2rem;
  line-height: 1.8;
  color: #fff;
  max-width: 800px;
  margin: 0 auto;
}

@media (max-width: 768px) {
  .about {
    padding: 4rem 0;
  }

  .about-container {
    grid-template-columns: 1fr;
    gap: 3rem;
    padding: 0 1rem;
  }

  .about-title {
    font-size: 2.5rem;
    text-align: center;
  }

  .about-description {
    font-size: 1.1rem;
    text-align: center;
  }

  .about-highlights {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .highlight-item {
    padding: 1.2rem;
  }

  .highlight-icon {
    font-size: 2rem;
  }

  .image-frame {
    width: 280px;
    height: 280px;
  }

  .about-stats {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .stat-item {
    padding: 1.2rem;
  }

  .stat-number {
    font-size: 2rem;
  }

  .skills-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .skill-card {
    padding: 1.5rem;
  }

  .skill-icon {
    font-size: 2.5rem;
  }

  .about-cta {
    padding: 2rem;
    margin-top: 3rem;
  }

  .cta-text {
    font-size: 1.1rem;
  }

  .image-frame {
    animation: float 8s ease-in-out infinite;
  }
}

@media (max-width: 480px) {
  .about {
    padding: 3rem 0;
  }

  .about-title {
    font-size: 2rem;
  }

  .about-description {
    font-size: 1rem;
  }

  .highlight-item {
    padding: 1rem;
  }

  .highlight-icon {
    font-size: 1.8rem;
  }

  .highlight-title {
    font-size: 1rem;
  }

  .image-frame {
    width: 220px;
    height: 220px;
  }

  .stat-item {
    padding: 1rem;
  }

  .stat-number {
    font-size: 1.8rem;
  }

  .stat-label {
    font-size: 0.9rem;
  }

  .image-frame {
    animation: float 10s ease-in-out infinite;
  }
}

@media (prefers-reduced-motion: reduce) {
  .image-frame,
  .image-frame::before,
  .highlight-item,
  .stat-item {
    animation: none;
    transition: none;
  }
} 