.projects {
  padding: 6rem 0;
  background: linear-gradient(135deg, #000 0%, #0a0a0a 50%, #000 100%);
  position: relative;
  overflow: hidden;
}

.projects::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 30% 70%, rgba(0, 212, 255, 0.05) 0%, transparent 50%),
              radial-gradient(circle at 70% 30%, rgba(0, 212, 255, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

.projects-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  z-index: 1;
}

.projects-header {
  text-align: center;
  margin-bottom: 4rem;
}

.projects-title {
  font-family: 'Orbitron', monospace;
  font-size: 3rem;
  font-weight: 900;
  margin-bottom: 1rem;
  color: #00d4ff;
  text-shadow: 0 0 20px rgba(0, 212, 255, 0.3);
}

.projects-subtitle {
  font-size: 1.2rem;
  color: #ccc;
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
  margin-bottom: 3rem;
}

.project-card {
  background: rgba(0, 212, 255, 0.05);
  border: 1px solid rgba(0, 212, 255, 0.2);
  border-radius: 15px;
  overflow: hidden;
  transition: all 0.3s ease;
  will-change: transform, box-shadow;
  position: relative;
}

.project-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(0, 212, 255, 0.05), transparent);
  transition: left 0.5s;
  z-index: 1;
}

.project-card:hover::before {
  left: 100%;
}

.project-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 20px 50px rgba(0, 212, 255, 0.2);
  border-color: rgba(0, 212, 255, 0.4);
}

.project-image {
  width: 100%;
  height: 200px;
  background: linear-gradient(45deg, #00d4ff, #0099cc);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.project-image::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, rgba(0, 212, 255, 0.3), rgba(0, 153, 204, 0.3));
  opacity: 0;
  transition: opacity 0.3s ease;
  will-change: opacity;
}

.project-card:hover .project-image::before {
  opacity: 1;
}

.project-placeholder {
  font-family: 'Orbitron', monospace;
  font-size: 3rem;
  font-weight: 900;
  color: #000;
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.project-content {
  padding: 1.5rem;
}

.project-title {
  font-family: 'Rajdhani', sans-serif;
  font-size: 1.3rem;
  font-weight: 600;
  color: #fff;
  margin-bottom: 0.5rem;
}

.project-description {
  font-size: 0.9rem;
  color: #ccc;
  line-height: 1.5;
  margin-bottom: 1rem;
}

.project-tech {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
}

.tech-tag {
  background: rgba(0, 212, 255, 0.2);
  border: 1px solid rgba(0, 212, 255, 0.3);
  border-radius: 20px;
  padding: 0.3rem 0.8rem;
  font-size: 0.8rem;
  color: #00d4ff;
  font-weight: 500;
}

.project-links {
  display: flex;
  gap: 1rem;
}

.project-link {
  flex: 1;
  background: linear-gradient(45deg, #00d4ff, #0099cc);
  border: none;
  color: #000;
  font-family: 'Rajdhani', sans-serif;
  font-weight: 600;
  font-size: 0.9rem;
  padding: 0.8rem 1rem;
  border-radius: 25px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  will-change: transform, box-shadow;
}

.project-link:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 212, 255, 0.3);
}

.project-link.secondary {
  background: transparent;
  border: 1px solid rgba(0, 212, 255, 0.3);
  color: #00d4ff;
}

.project-link.secondary:hover {
  background: rgba(0, 212, 255, 0.1);
  border-color: #00d4ff;
}

.featured-projects {
  margin-top: 4rem;
}

.featured-title {
  font-family: 'Orbitron', monospace;
  font-size: 2rem;
  font-weight: 700;
  color: #00d4ff;
  margin-bottom: 2rem;
  text-align: center;
  text-shadow: 0 0 10px rgba(0, 212, 255, 0.3);
}

.featured-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.featured-card {
  background: rgba(0, 212, 255, 0.08);
  border: 2px solid rgba(0, 212, 255, 0.3);
  border-radius: 20px;
  padding: 2rem;
  text-align: center;
  transition: all 0.3s ease;
  will-change: transform, box-shadow;
  position: relative;
  overflow: hidden;
}

.featured-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(0, 212, 255, 0.1), transparent);
  transition: left 0.5s ease;
  will-change: left;
}

.featured-card:hover::before {
  left: 100%;
}

.featured-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(0, 212, 255, 0.2);
  border-color: #00d4ff;
}

.featured-icon {
  font-size: 3rem;
  color: #00d4ff;
  margin-bottom: 1rem;
}

.featured-title-item {
  font-family: 'Rajdhani', sans-serif;
  font-weight: 600;
  font-size: 1.2rem;
  color: #fff;
  margin-bottom: 0.5rem;
}

.featured-description {
  font-size: 0.9rem;
  color: #ccc;
  line-height: 1.5;
  margin-bottom: 1rem;
}

.featured-link {
  background: linear-gradient(45deg, #00d4ff, #0099cc);
  border: none;
  color: #000;
  font-family: 'Rajdhani', sans-serif;
  font-weight: 600;
  font-size: 0.9rem;
  padding: 0.8rem 1.5rem;
  border-radius: 25px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  will-change: transform, box-shadow;
}

.featured-link:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 212, 255, 0.3);
}

/* Otimizações para mobile */
@media (max-width: 768px) {
  .projects {
    padding: 4rem 0;
  }

  .projects-container {
    padding: 0 1rem;
  }

  .projects-title {
    font-size: 2.5rem;
  }

  .projects-subtitle {
    font-size: 1.1rem;
  }

  .projects-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .project-card {
    margin-bottom: 1rem;
  }

  .project-image {
    height: 180px;
  }

  .project-placeholder {
    font-size: 2.5rem;
  }

  .project-content {
    padding: 1.2rem;
  }

  .project-title {
    font-size: 1.2rem;
  }

  .project-description {
    font-size: 0.85rem;
  }

  .project-links {
    flex-direction: column;
    gap: 0.8rem;
  }

  .project-link {
    font-size: 0.85rem;
    padding: 0.7rem 1rem;
  }

  .featured-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .featured-card {
    padding: 1.5rem;
  }

  .featured-icon {
    font-size: 2.5rem;
  }

  .featured-title-item {
    font-size: 1.1rem;
  }

  .featured-description {
    font-size: 0.85rem;
  }

  .featured-link {
    font-size: 0.85rem;
    padding: 0.7rem 1.2rem;
  }

  /* Otimizar animações em mobile */
  .project-card:hover {
    transform: translateY(-5px);
  }

  .featured-card:hover {
    transform: translateY(-3px);
  }
}

@media (max-width: 480px) {
  .projects {
    padding: 3rem 0;
  }

  .projects-title {
    font-size: 2rem;
  }

  .projects-subtitle {
    font-size: 1rem;
  }

  .project-image {
    height: 150px;
  }

  .project-placeholder {
    font-size: 2rem;
  }

  .project-content {
    padding: 1rem;
  }

  .project-title {
    font-size: 1.1rem;
  }

  .project-description {
    font-size: 0.8rem;
  }

  .tech-tag {
    font-size: 0.7rem;
    padding: 0.2rem 0.6rem;
  }

  .project-link {
    font-size: 0.8rem;
    padding: 0.6rem 0.8rem;
  }

  .featured-card {
    padding: 1.2rem;
  }

  .featured-icon {
    font-size: 2rem;
  }

  .featured-title-item {
    font-size: 1rem;
  }

  .featured-description {
    font-size: 0.8rem;
  }

  .featured-link {
    font-size: 0.8rem;
    padding: 0.6rem 1rem;
  }

  /* Desabilitar animações complexas em dispositivos muito pequenos */
  .project-card:hover,
  .featured-card:hover {
    transform: none;
  }

  .featured-card::before {
    display: none;
  }
}

/* Otimizações de performance */
@media (prefers-reduced-motion: reduce) {
  .project-card,
  .featured-card,
  .project-link,
  .featured-link,
  .project-image::before,
  .featured-card::before {
    animation: none;
    transition: none;
  }
} 