.skills {
  padding: 6rem 0;
  background: linear-gradient(135deg, #000 0%, #0a0a0a 50%, #000 100%);
  position: relative;
  overflow: hidden;
}

.skills::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 50% 20%, rgba(0, 212, 255, 0.03) 0%, transparent 50%),
              radial-gradient(circle at 80% 80%, rgba(0, 212, 255, 0.03) 0%, transparent 50%);
  pointer-events: none;
}

.skills-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  z-index: 1;
}

.skills-header {
  text-align: center;
  margin-bottom: 4rem;
}

.skills-title {
  font-family: 'Orbitron', monospace;
  font-size: 3rem;
  font-weight: 900;
  margin-bottom: 1rem;
  color: #00d4ff;
  text-shadow: 0 0 20px rgba(0, 212, 255, 0.3);
}

.skills-subtitle {
  font-size: 1.2rem;
  color: #ccc;
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-bottom: 4rem;
}

.skill-category {
  background: rgba(0, 212, 255, 0.05);
  border: 1px solid rgba(0, 212, 255, 0.2);
  border-radius: 15px;
  padding: 2rem;
  transition: all 0.3s ease;
  will-change: transform, box-shadow;
}

.skill-category:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(0, 212, 255, 0.15);
  border-color: rgba(0, 212, 255, 0.4);
}

.category-title {
  font-family: 'Orbitron', monospace;
  font-size: 1.5rem;
  font-weight: 700;
  color: #00d4ff;
  margin-bottom: 1.5rem;
  text-align: center;
  text-shadow: 0 0 10px rgba(0, 212, 255, 0.3);
}

.skills-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 1rem;
}

.skill-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(0, 212, 255, 0.1);
  border-radius: 10px;
  transition: all 0.3s ease;
  will-change: transform, box-shadow;
}

.skill-item:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(0, 212, 255, 0.2);
  border-color: rgba(0, 212, 255, 0.3);
}

.skill-icon {
  font-size: 2.5rem;
  color: #00d4ff;
  margin-bottom: 0.5rem;
  transition: all 0.3s ease;
  will-change: transform;
}

.skill-item:hover .skill-icon {
  transform: scale(1.1);
}

.skill-name {
  font-family: 'Rajdhani', sans-serif;
  font-weight: 500;
  font-size: 0.9rem;
  color: #fff;
  text-align: center;
  margin-bottom: 0.5rem;
}

.skill-level {
  width: 100%;
  height: 4px;
  background: rgba(0, 212, 255, 0.1);
  border-radius: 2px;
  overflow: hidden;
  position: relative;
}

.skill-progress {
  height: 100%;
  background: linear-gradient(45deg, #00d4ff, #0099cc);
  border-radius: 2px;
  transition: width 1s ease;
  will-change: width;
}

.skill-percentage {
  position: absolute;
  top: -20px;
  right: 0;
  font-size: 0.7rem;
  color: #00d4ff;
  font-weight: 500;
}

.experience-section {
  margin-top: 4rem;
}

.experience-title {
  font-family: 'Orbitron', monospace;
  font-size: 2rem;
  font-weight: 700;
  color: #00d4ff;
  margin-bottom: 2rem;
  text-align: center;
  text-shadow: 0 0 10px rgba(0, 212, 255, 0.3);
}

.experience-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}

.experience-item {
  background: rgba(0, 212, 255, 0.05);
  border: 1px solid rgba(0, 212, 255, 0.2);
  border-radius: 15px;
  padding: 2rem;
  text-align: center;
  transition: all 0.3s ease;
  will-change: transform, box-shadow;
}

.experience-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 212, 255, 0.15);
  border-color: rgba(0, 212, 255, 0.4);
}

.experience-icon {
  font-size: 3rem;
  color: #00d4ff;
  margin-bottom: 1rem;
}

.experience-title-item {
  font-family: 'Rajdhani', sans-serif;
  font-weight: 600;
  font-size: 1.2rem;
  color: #fff;
  margin-bottom: 0.5rem;
}

.experience-description {
  font-size: 0.9rem;
  color: #ccc;
  line-height: 1.5;
}

/* Otimizações para mobile */
@media (max-width: 768px) {
  .skills {
    padding: 4rem 0;
  }

  .skills-container {
    padding: 0 1rem;
  }

  .skills-title {
    font-size: 2.5rem;
  }

  .skills-subtitle {
    font-size: 1.1rem;
  }

  .skills-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .skill-category {
    padding: 1.5rem;
  }

  .category-title {
    font-size: 1.3rem;
  }

  .skills-list {
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    gap: 0.8rem;
  }

  .skill-item {
    padding: 0.8rem;
  }

  .skill-icon {
    font-size: 2rem;
  }

  .skill-name {
    font-size: 0.8rem;
  }

  .experience-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .experience-item {
    padding: 1.5rem;
  }

  .experience-icon {
    font-size: 2.5rem;
  }

  .experience-title-item {
    font-size: 1.1rem;
  }

  /* Otimizar animações em mobile */
  .skill-item:hover .skill-icon {
    transform: scale(1.05);
  }
}

@media (max-width: 480px) {
  .skills {
    padding: 3rem 0;
  }

  .skills-title {
    font-size: 2rem;
  }

  .skills-subtitle {
    font-size: 1rem;
  }

  .skill-category {
    padding: 1.2rem;
  }

  .category-title {
    font-size: 1.2rem;
  }

  .skills-list {
    grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
    gap: 0.6rem;
  }

  .skill-item {
    padding: 0.6rem;
  }

  .skill-icon {
    font-size: 1.8rem;
  }

  .skill-name {
    font-size: 0.7rem;
  }

  .experience-item {
    padding: 1.2rem;
  }

  .experience-icon {
    font-size: 2rem;
  }

  .experience-title-item {
    font-size: 1rem;
  }

  .experience-description {
    font-size: 0.8rem;
  }

  /* Desabilitar animações complexas em dispositivos muito pequenos */
  .skill-item:hover .skill-icon {
    transform: none;
  }
}

/* Otimizações de performance */
@media (prefers-reduced-motion: reduce) {
  .skill-category,
  .skill-item,
  .experience-item,
  .skill-icon,
  .skill-progress {
    animation: none;
    transition: none;
  }
} 